import Vue from 'vue'
import Router from 'vue-router'
import MainPage from '../components/MainPage'
import AddTable from '../components/AddTable'
import ShowUser from '../components/user/ShowUser'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'MainPage',
      component: MainPage
    },
    {
      path: '/result',
      name: 'MainPage',
      component: MainPage
    },
    {
      path: '/user',
      name: 'user',
      component: ShowUser
    },
    {
      path: '/create',
      name: 'MainPage',
      component: AddTable,
      children: [
        {
          path: '/table',
          component: MainPage
        }
      ]
    }
  ]
})



