<template>
  <div class="container">
    <button class="btn-primary btn my-5" v-on:click="AddUser">사용자 추가</button>
    <button class="btn-primary btn my-5" v-on:click="getUser">사용자 받기</button>
    <table class="table">
      <thead class="thead-dark">
      <tr>
        <th scope="col">#</th>
        <th scope="col">이름</th>
        <th scope="col">이메일</th>
        <th scope="col">직책</th>
        <th scope="col">팀 이름</th>
      </tr>
      </thead>
      <tbody>
      <tr v-for="(member, index) in this.memberList" v-bind:key="member.name">
        <th scope="row">{{ index+1 }}</th>
        <td>{{ member.name }}</td>
        <td>{{ member.email }}</td>
        <td>{{ member.position }}</td>
        <td>{{ member.teamName }}</td>
      </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'ShowUser',
  methods: {
    AddUser () {
      var user = {
        'name': '윤성재',
        'email': 'dbstjdwo1000@naver.com',
        'position': '사원',
        'teamName': '인증플랫폼'
      }
      this.memberList.push(user)
    },
    getUser () {
      // axios.get('http://localhost:8080/users')
      //   .then(function (response) {
      //     console.log(response)
      //   })
      //   .catch(function (error) {
      //     console.log(error)
      //   })
      axios({
        method: 'get',
        url: 'http://localhost:8080/users',
        responseType: 'json'
      })
        .then(function (response) {
          console.log(response)
        })
    }
  },
  data: function () {
    var member1 = {
      'name': '윤성재',
      'email': 'dbstjdwo1000@naver.com',
      'position': '사원',
      'teamName': '인증플랫폼'
    }
    var member2 = {
      'name': '재윤성',
      'email': 'dbstjdwo0000@gmail.com',
      'position': '대리',
      'teamName': '결제플랫폼'
    }
    return {
      memberList: [ member1, member2 ]
    }
  }
}
</script>

<style scoped>

</style>



